import React from "react"
import { useIntersection } from 'react-use';
import {gsap} from "gsap"

const Heading = ({title,desc}) => {
    const headingRef = React.useRef(null)
    const observer = useIntersection(headingRef,{
        ref:null,
        rootMargin: '0px',
        threshold: 1
    })
    gsap.to(headingRef.current,{duration:0,y:100,opacity:0})
    observer && observer.intersectionRatio < 1 ? gsap.to(headingRef.current,{}) : gsap.to(headingRef.current,{duration:.4,y:0,opacity:1})
    return (
        <div ref={headingRef}>
            <h3 className=" global-Heading mb-4">{title}</h3>
            <p className="global-desc mb-4">{desc}</p>
        </div>
    )
}



export default Heading