import React from "react"
import data from "../../../jsons/headerNews"

import {Carousel} from "react-bootstrap"
import {gsap} from "gsap"
import {useIntersection} from "react-use"
import {addClassToNav} from "../../../actions"
import {connect} from "react-redux"
const RenderSlide = (props) => {
    const {title,description} = props
    const captionRef = React.useRef(null)
        const observer = useIntersection(captionRef,{
            ref:null,
            rootMargin: '0px',
            threshold: .4
        })
        if(window.innerWidth >= 992){
            gsap.to(captionRef.current,{duration:0,y:100,opacity:0})
            observer && observer.intersectionRatio < .4 ? gsap.to(captionRef.current,{}) : gsap.to(captionRef.current,{duration:.6,y:0,opacity:1})
        }
        else{
            gsap.to(captionRef.current,{duration:0,xPercent:-50,yPercent:-30,opacity:0})
            observer && observer.intersectionRatio < .4 ? gsap.to(captionRef.current,{duration:0,xPercent:-50,yPercent:-30,opacity:0}) : gsap.to(captionRef.current,{duration:.6,xPercent:-50,yPercent:-50,opacity:1})
        }
        return (
            <div ref={captionRef} className=" caption">
                <span>
                    <h6>WINKEL ECOMMERCE SHOP</h6>
                    <h2>{title}</h2>
                    <p>{description}</p>
                    <h5>BEST ECOMMERCE SHOP ONLINE</h5>
                    <button>Discover Now</button>
                </span>
            </div>
        
    )
}
const Header = (props) => {
    console.log(props)
    const headerRef = React.useRef(null)
    console.log(headerRef)
    const observer = useIntersection(headerRef,{
        ref:null,
        rootMargin: '0px',
        threshold: .1
    })
    
    observer && observer.intersectionRatio < .1 ? props.addClassToNav("fixed") : props.addClassToNav("")
    
    return (
        <div  ref={headerRef}>
            <Carousel touch={true} controls={false} pause={false} interval={3000} >
                {data().map((item,id) => (
                    <Carousel.Item key={id}>
                        <div className="slide">
                            <div className="img" style={{background:`url(${item.img})`,backgroundSize:"cover",backgroundPosition:"center center"}}>

                            </div>
                            <RenderSlide  title={item.title} description={item.description} />

                        </div>
                    </Carousel.Item>
                ))}
            </Carousel>
        </div>
    )
}


export default connect(null,{addClassToNav})(Header)